<template >
  <div class="review-experience">
    <!-- NAVBAR -->
    <navbar></navbar> 
    <!-- Table & Image -->
    <br> <br>
      <b-container>
        <b-row>
          <b-col>
            <img align="right" id="img-wcu" :src="review.imageURL" >
          </b-col>
          <b-col>
            <table class="table table-borderless">
              <thead>
                <tr>
                  <th 
                    scope="col" 
                    style="font-size: 20px; 
                          color: #000000">
                   <b>{{ review.service }}</b>
                  </th>
                </tr>
              </thead>
              <thead>
                <tr>
                  <th 
                    scope="col"
                    style="font-size: 16px; 
                          color: #6F51C7;"
                  >
                    <b>{{ review.cliente }}</b>
                  </th>
                </tr>
              </thead>
              <tbody>
                <tr>
                  <td 
                    style="font-size: 16px; 
                          color: #828282">
                    {{ review.description }}
                  </td>
                </tr>
              </tbody>
            </table>
          </b-col>
        </b-row>
      </b-container>
      <div class="container">
        <h4>
          <b>CUSTOMER EVALUATIONS</b>
        </h4>
          <!-- Contenedor Principal -->
        <div class="comments-container">
          <ul id="comments-list" class="comments-list">
            <li v-for="item in comment" :key="item.id">
              <div class="comment-main-level">
                <!-- Avatar -->
                <div class="comment-avatar"><img src="../assets/013-crown.png" alt="teeth-photo"></div>
                <!-- Contenedor del Comentario -->
                <div class="comment-box">
                  <div class="comment-head">
                    <h6 class="comment-name by-author"><p >{{item.name.toUpperCase()}}</p></h6>
                    <span> | {{item.date}}</span>
                  </div>
                  <div class="comment-content">
                    {{item.text}}
                  </div>
                </div>
              </div>
            </li>
          </ul>
        </div>
      </div>
		<div class="container" style=" width: 800px;
      height: 110px;">
		<h2 id="subtitle">Write us your opinion!</h2>
        <div>
            <b-form-group
              id="fieldset-1"
              description="Let us know your name."
              label="Enter your name"
              label-for="input-1"
              :invalid-feedback="invalidFeedback"
              :valid-feedback="validFeedback"
              :state="state"
              >
            <b-form-input id="input-1" v-model="name" :state="state" trim></b-form-input>
            </b-form-group>

            <b-form-textarea
                id="textarea"
                v-model="text"
                placeholder="Enter something..."
                rows="3"
                max-rows="6"
                maxlength="1500"
                :state="validation"
                >
            </b-form-textarea>
            <b-form-invalid-feedback :state="validation">
              Your comment must be 1500 characters long.
            </b-form-invalid-feedback>
            <b-form-valid-feedback :state="validation">
              Looks Good.
            </b-form-valid-feedback>
          <div id="enviar-comentario"> 
            <b-button id="btn-enviar-comentario" size="lg" pill type="submit" @click="addcomment">LEAVE YOUR COMMENTS</b-button>
          </div>
        </div>
    </div>
  </div>
</template>

<script>
import navbar from '../components/navbar'
export default {

  data() {
    return {
      name:"",
      text:"",
      reference: this.$route.params.id,
      review: {},
      comment: []
    }
  },
  created(){
    this.init();
    this.init2();
  },
  computed:{
    id(){
      return this.$route.params.id
    },
    validation() {
        return this.text.length > 4 && this.text.length < 13
      },
    state(){
      return this.name.length >= 4? true : false
    },
    invalidFeedback(){
      if (this.name.length > 4) {
          return ''
        } else if (this.name.length > 0) {
          return 'Enter at least 4 characters'
        } else {
          return 'Please enter something'
        }
    },
    validFeedback(){
      return this.state === true ? 'Thank you' : ''
    },
  },
  methods:{

    addcomment(){
			this.axios.post('http://localhost:3000/comment/'+this.id,{
        name:this.name,
        text:this.text,
        reference: this.comment.id
			})
			.then(res =>{
      console.log('obtenidos satisfactoriamente'+res.data)
			})
			.catch((erro) => {
			console.log('error innesperado'+erro.response);
      })
		},
    init()
    {
      fetch('http://localhost:3000/Reviews/'+this.id)
        .then(res => res.json())
        .then(data => this.review=data)
    }	,
    init2(){
			fetch('http://localhost:3000/comment/'+this.id)
				.then(res => res.json())
				.then(data => this.comment=data)
		}
  },
  components: {
    navbar,
  },
}
</script>

<style lang="scss" scoped>
@import url('https://fonts.googleapis.com/css?family=Lato|Montserrat&display=swap');
@import url('https://fonts.googleapis.com/css?family=Lato&display=swap');

  .review-experience{
    font-family: 'Montserrat';
    margin: 70px;
  }

  .comments-container{
      overflow: auto;
      width: 310px;
      height: 310px;
  }

  ::-webkit-scrollbar {
  width: 10px;
  }

  /* Track */
  ::-webkit-scrollbar-track {
    background: #f1f1f1; 
  }
  
  /* Handle */
  ::-webkit-scrollbar-thumb {
    background: #888; 
  }

  /* Handle on hover */
  ::-webkit-scrollbar-thumb:hover {
    background: #555; 
  }

  h4{
    color: #333333;
    text-align: center;
    padding-top: 60px;
  }

  tr{
    text-align: left;
  }

  hr{
    height: 1px;
    background-color: #ffffff;
  }

  .container{
    text-align: left;
  }

  #img-wcu{
    max-width: 550px;
    max-height: 400px;
    margin-left: 120px;
  }
  #icon-teeth{
		width: 50%;
    height: 50%;
	}

	#subtitle{
		font-size: 20px;
		font-weight: bold;
		margin-top: 40px;
		margin-bottom: 20px;
	}

  ul {
    list-style-type: none;
  }

  body {
    font-family: 'Montserrat';
    background: #dee1e3;
  }

  #fieldset-1 {
    font-size: 12px;
    padding-top: 20px;
  }
      
  #enviar-comentario{
    padding-top: 20px;
    padding-bottom: 20px;
    text-align: right;	
  }

  #btn-enviar-comentario{
    font-size: 12px;
    background-color: #6F51C7;
    border: 10px solid #6F51C7;
  }

  /** ====================
  * Lista de Comentarios
  =======================*/
  .comments-container {
    margin: 53px auto 5px;
    width: 768px;
  }

  .comments-container h1 {
    font-size: 36px;
    color: #283035;
    font-weight: 400;
  }

  /**
  * Lineas / Detalles
  -----------------------*/
  .comments-list li {
    margin-bottom: 15px;
    display: block;
    position: relative;

  }

  #comments-list{
    padding-left: 0px;
  }

  .comments-list li:after {
    content: '';
    display: block;
    clear: both;
    height: 0;
    width: 0;
  }

  /**
  * Avatar
  ---------------------------*/
  .comments-list .comment-avatar {
    width: 65px;
    height: 65px;
    position: relative;
    z-index: 99;
    float: left;
    overflow: hidden;
  }

  .comments-list .comment-avatar img {
    width: 50px;
    height: 50px;
    border-radius: 50%;
  }

  .reply-list .comment-avatar {
    width: 50px;
    height: 50px;
  }

  /**
  * Caja del Comentario
  ---------------------------*/
  .comments-list .comment-box {
    width: 680px;
    float: right;
    position: relative;
    -webkit-box-shadow: 0 1px 1px rgba(0,0,0,0.15);
    -moz-box-shadow: 0 1px 1px rgba(0,0,0,0.15);
    box-shadow: 0 1px 1px rgba(0,0,0,0.15);
  }

  .comment-box .comment-head {
    background: #6F51C7;
    padding: 10px 12px;
    border-bottom: 1px solid #6F51C7;
    overflow: hidden;
    -webkit-border-radius: 4px 4px 0 0;
    -moz-border-radius: 4px 4px 0 0;
    border-radius: 4px 4px 0 0;
  }

  .comment-box .comment-name {
    font-size: 14px;
    font-weight: 700;
    float: left;
    margin-right: 10px;
    margin-bottom: 0px;
  }

  .comment-box .comment-name p {
    color: #ffffff;
    margin-bottom: 0px;
  }

  .comment-box .comment-head span {
    float: left;
    color: #ffffff;
    font-size: 10px;
    position: relative;
    top: 1px;
  }

  .comment-box .comment-content {
    background: #FFF;
    padding: 12px;
    font-size: 14px;
    color: #595959;
    -webkit-border-radius: 0 0 4px 4px;
    -moz-border-radius: 0 0 4px 4px;
    border-radius: 0 0 4px 4px;
  }

  /** =====================
  * Responsive
  ========================*/
  @media only screen and (max-width: 766px) {
    .comments-container {
      width: 280px;
    }

    .comments-list .comment-box {
      width: 190px;
    }

    .reply-list .comment-box {
      width: 120px;
    }
  }
</style>